/*
* Site fonts 
* 
* Enqueued in functions.php 
*/
body,
button {
		font-family: "Roboto", sans-serif;
		font-style: normal;
		color: #414840;
		line-height: 150%;
}

h1,
h2,
h3,
h4,
h5,
h6,
.entry-title {
		font-family: "Libre Baskerville", serif;
		color: #343d33;
		font-weight: initial;
		font-style: normal;
		line-height: 110%;
		text-transform: capitalize;
}

/*
* Font Sizing
*/
body,
p,
a {
		font-size: 14px;
}
@media screen and (min-width: 320px) {
		body,
p,
a {
				font-size: calc(
					14px + 4 *
						((100vw - 320px) / 880)
				);
		}
}
@media screen and (min-width: 1200px) {
		body,
p,
a {
				font-size: 18px;
		}
}

h1 {
		font-size: 40px;
}
@media screen and (min-width: 320px) {
		h1 {
				font-size: calc(
					40px + 20 *
						((100vw - 320px) / 880)
				);
		}
}
@media screen and (min-width: 1200px) {
		h1 {
				font-size: 60px;
		}
}

h2 {
		font-size: 30px;
}
@media screen and (min-width: 320px) {
		h2 {
				font-size: calc(
					30px + 15 *
						((100vw - 320px) / 880)
				);
		}
}
@media screen and (min-width: 1200px) {
		h2 {
				font-size: 45px;
		}
}

h3 {
		font-size: 18px;
}
@media screen and (min-width: 320px) {
		h3 {
				font-size: calc(
					18px + 8 *
						((100vw - 320px) / 880)
				);
		}
}
@media screen and (min-width: 1200px) {
		h3 {
				font-size: 26px;
		}
}

h4 {
		font-size: 16px;
}
@media screen and (min-width: 320px) {
		h4 {
				font-size: calc(
					16px + 8 *
						((100vw - 320px) / 880)
				);
		}
}
@media screen and (min-width: 1200px) {
		h4 {
				font-size: 24px;
		}
}

