/*
* Header styling
*/
.site-header .navbar {
		width: 100%;
}
.site-header .navbar .nav-container {
		position: relative;
		height: 80px;
}
.site-header .navbar .nav-container .check-container {
		position: relative;
		width: 32px;
}
.site-header .navbar .nav-container .check-container .checkbox {
		position: absolute;
		display: block;
		height: 32px;
		width: 32px;
		z-index: 100;
		opacity: 0;
		cursor: pointer;
}
.site-header .navbar .nav-container .check-container .hamburger-lines {
		display: block;
		height: 26px;
		width: 32px;
		position: absolute;
		z-index: 99;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
}
.site-header .navbar .nav-container .check-container .hamburger-lines .line {
		display: block;
		height: 4px;
		width: 100%;
		border-radius: 10px;
		background-color: #343d33;
}
.site-header .navbar .nav-container .check-container .hamburger-lines .line1 {
		transform-origin: 0% 0%;
		transition: transform 0.5s ease-in;
}
.site-header .navbar .nav-container .check-container .hamburger-lines .line2 {
		transition: transform 0.2s ease-in-out;
}
.site-header .navbar .nav-container .check-container .hamburger-lines .line3 {
		transform-origin: 0% 100%;
		transition: transform 0.5s ease-in;
}
.site-header .navbar .nav-container #primary-menu ul {
		display: flex;
}
.site-header .navbar .nav-container #primary-menu li {
		list-style: none;
		padding: 0 1rem;
}
.site-header .navbar .nav-container #primary-menu li a {
		color: #343d33;
}
.site-header .navbar .nav-container #primary-menu li a.active {
		font-weight: bolder;
		color: #2e6e3c;
}
