/*
* Button Styles
*/
.btn {
		border-radius: 0;
		text-transform: uppercase;
		font-size: 14px;
		border: none;
		padding: 1rem 2.5rem;
		border: 2px solid #2e6e3c;
		transition: all 300ms ease-in-out;
}
.btn--primary {
		color: #ffffff;
		background-color: #2e6e3c;
}
.btn--secondary {
		background-color: transparent;
		color: #2e6e3c;
}
.btn--tertiary {
		background-color: transparent;
		border-color: #ffffff;
		color: #ffffff;
}
.btn:hover {
		opacity: 0.7;
		cursor: pointer;
}

